@tailwind base;
@tailwind components;
@tailwind utilities;

body{
  font-family: "Arial";
  background-color:#2157c5;
  color: white;
  width: 100%;
  height: 100%;
}

.header{
  width: 100%;
  height: 90px;
}
.footer{
  width: 100%;
  height: 250px;
}

.container{
  
  height: 500px;
  width: 100%;
  
}

.container-l1{
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 200px, auto, 200px;
  align-items: center;
  justify-content: center;
}

.container-l2{
  
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}

.input-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

}

.input-container-l2{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button-container{
  
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button{
  background-color: #e11d48;
  width: 100px;
  height: 32px;
  border-radius: 20px;
  font-weight: bold;
}

.name-label{
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 10px;
}

.input{
  border-radius: 3px;
  width: 400px;
  color: black;
}

.connect-button{
  background-color: #e11d48;
  width: 100px;
  height: 32px;
  border-radius: 20px;
  font-weight: bold; 
}